import React from 'react';
import connect from '@vkontakte/vkui-connect';
import { View, Panel, Div, PanelHeader, HeaderButton, Tabs, TabsItem, HorizontalScroll, Search, List, Cell, Avatar, Spinner, ScreenSpinner, platform, IOS, ANDROID } from '@vkontakte/vkui';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Search from '@vkontakte/icons/dist/28/search'
import '@vkontakte/vkui/dist/vkui.css';
import './App.css';

//import Home from './panels/Home/Home';
//import Main from './panels/Main/Main';
//import Persik from './panels/Persik/Persik';
const osname = platform();

const users = [
	{ id: 3201, name: "Влад Анесов", domain: 'vladacsenov' },
	{ id: 3273, name: "Вадим Дорохов", domain: 'vadimdorohov' },
	{ id: 3205, name: "Андрей Абрамов", domain: 'andreyabramov' },
	{ id: 3282, name: "Александр Колобов", domain: 'aleksandrkolobov' },
	{ id: 3283, name: "Артур Стамбульцян", domain: 'arturstam' },
	{ id: 3284, name: "Илья Таратухин", domain: 'ilyataratuhin' },
	{ id: 3285, name: "Роман Захаров", domain: 'romanzaharov' },
	{ id: 3286, name: "Михаил Андриевский", domain: 'michail' },
	{ id: 3287, name: "Тарас Иванов", domain: 'taras' },
	{ id: 3288, name: "Илья Гришин", domain: 'ilyagrishin' },
	{ id: 3117, name: "Илья Пеняев", domain: 'ilyapenyaev' },
	{ id: 3118, name: "Иван Барышев", domain: 'ivanbarishev' },
];

function Greeting(props) {
	const active = props.activeTab;
	if (active === 'all') {
		return 'all';
	} else if (active === 'users') {
		return 'users';
	} else {
		return 'groups';
	}
}

function Monero(props) {
	const count = props.count;
	return <div>{count} <i className="fab fa-monero"></i></div>;
}

class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			activePanel: 'main',
			activeTab6: 'all',
			showSearch: osname === IOS,
			search: '',
			popout: null
		};
		this.toggleSearch = this.toggleSearch.bind(this);

		this.onChange = this.onChange.bind(this);
	}

	componentDidMount() {
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
					this.setState({ fetchedUser: e.detail.data });
					break;
				default:
					console.log(e.detail.type);
			}
		});
		connect.send('VKWebAppGetUserInfo', {});
	}

	toggleSearch() { this.setState({ showSearch: !this.state.showSearch }); }

	onChange(search) { this.setState({ search }); }

	onClick() {
		this.setState({ popout: <ScreenSpinner /> });
		setTimeout(() => { this.setState({ popout: null }) }, 2000);
	}

	get users() {
		const search = this.state.search.toLowerCase();
		let outputByName = users.filter(({ name }) => name.toLowerCase().indexOf(search) > -1);
		let outputByDomain = users.filter(({ domain }) => domain.toLowerCase().indexOf(search) > -1);
		//console.log(outputByName)
		if (outputByName.length > 0) {
			return outputByName;
		} else {
			return outputByDomain;
		}
	}

	go = (e) => {
		this.setState({ activePanel: e.currentTarget.dataset.to })
	};

	test = () => {
		return (<Spinner size="small" style={{ marginTop: 20 }} />);
	}

	render() {
		return (
			<View activePanel={this.state.activePanel} popout={this.state.popout}>
				<Panel id="main">
					<Div>
						<div className="row">
							<div className="col-md-12">
								<div className="text-center text-muted coins__block fz-20">
									Ваши коины
					     		</div>
								<div className="text-center font-weight-bold fz-30 mt-2">
									0 <i className="fab fa-monero"></i>
								</div>
								<div className="text-center buttons">
									<div className="d-flex justify-content-center">
										<div className="col-md-3">
											<button className="btn-rating" onClick={() => this.setState({ activePanel: 'rating' })}><i className="fas fa-signal"></i></button>
											<p className="text-buttons">Рейтинг</p>
										</div>
										<div className="col-md-3">
											<button className="btn-exchange" onClick={() => this.setState({ activePanel: 'exchange' })}><i className="fas fa-exchange-alt"></i></button>
											<p className="text-buttons">Обменник</p>
										</div>
										<div className="col-md-3">
											<button className="btn-game"><i className="fas fa-gamepad"></i></button>
											<p className="text-buttons">Игры</p>
										</div>
										<div className="col-md-3">
											<button className="btn-shop"><i className="fas fa-shopping-bag"></i></button>
											<p className="text-buttons">Магазин</p>
										</div>
									</div>
								</div>
								<div className="text-center">
									<i className="fab fa-monero click__item"></i>
								</div>
							</div>
						</div>
					</Div>
				</Panel>
				<Panel id="rating">
					<PanelHeader
						noShadow
						left={<HeaderButton onClick={() => this.setState({ activePanel: 'main' })}>{osname === IOS ? <Icon28ChevronBack /> : <Icon24Back />}</HeaderButton>}
						addon={<HeaderButton onClick={() => this.setState({ activePanel: 'main' })}>Назад</HeaderButton>}
					>
						Рейтинг
          			</PanelHeader>
					<Tabs theme="header" type="buttons">
						<HorizontalScroll>
							<TabsItem
								onClick={() => this.setState({ activeTab6: 'all' })}
								selected={this.state.activeTab6 === 'all'}
							>
								Все
                  			</TabsItem>
							<TabsItem
								onClick={() => this.setState({ activeTab6: 'users' })}
								selected={this.state.activeTab6 === 'users'}
							>
								Люди
                  			</TabsItem>
							<TabsItem
								onClick={() => this.setState({ activeTab6: 'groups' })}
								selected={this.state.activeTab6 === 'groups'}
							>
								Сообщества
                  			</TabsItem>
						</HorizontalScroll>
					</Tabs>
					<Greeting activeTab={this.state.activeTab6} />
				</Panel>
				<Panel id="exchange">
					<PanelHeader
						left={<HeaderButton onClick={() => this.setState({ activePanel: 'main' })}>{osname === IOS ? <Icon28ChevronBack /> : <Icon24Back />}</HeaderButton>}
						right={osname === ANDROID && <HeaderButton onClick={this.toggleSearch}><Icon24Search /></HeaderButton>}
					>
					Перевод
					</PanelHeader>
					<Search value={this.state.search} onChange={this.onChange} />
					<List>
						{this.users.map((user) => (
							<Cell
								before={<Avatar size={40} src="https://pp.userapi.com/c841034/v841034569/3b8c1/pt3sOw_qhfg.jpg" />}
								key={user.id}
								description={<Monero count={1000} />}
								onClick={this.onClick.bind(this)}
							>
								{user.name}
							</Cell>
						))}
					</List>
				</Panel>
			</View>
		);
	}
}

export default App;
